
import axios from 'axios';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Login',
  props: ['message'],
  emits: ['emit-snackbar'],
  components: {
  },
  mounted() {
    if (this.$store.getters.getUsername) {
      this.checkSkillsAdded();
    }
  },
  watch: {
  },
  methods: {
    loginUser() {
      if (!this.message.length) {
        axios({
          method: 'POST',
          url: 'http://localhost:8000/login',
          data: {
            username: this.username,
            password: this.password,
          },
        }).then((response) => {
          if (response.data.message === 'username or password doesn\'t match!') {
            this.$emit('emit-snackbar', response.data.message);
          } else {
            this.$store.dispatch('setUsername', this.username);
            localStorage.setItem('username', this.username);
            this.$store.dispatch('setIsAdmin', response.data.isAdmin);
            localStorage.setItem('isAdmin', response.data.isAdmin);
            this.checkSkillsAdded();
          }
        });
      }
    },
    checkSkillsAdded() {
      axios({
        method: 'POST',
        url: 'http://localhost:8000/register',
        data: {
          username: this.$store.getters.getUsername,
          action: 'skills_check',
        },
      }).then((response) => {
        if (response.data.skills.length > 2) {
          this.$router.push('/recommend');
        } else {
          this.$router.push('/skills');
        }
      });
    },
  },
  data() {
    return {
      username: '',
      password: '',
    };
  },
});
