
import { defineComponent } from 'vue';
import axios from 'axios';
import Loading from '../components/Loading.vue';

export default defineComponent({
  name: 'EditProfile',
  props: ['message', 'rec-jobs'],
  emits: ['emit-snackbar'],
  components: {
    Loading,
  },
  computed: {
    skills_filtered() {
      return this.search_term
        ? this.skills.filter((skill) => skill.toLowerCase().includes(this.search_term))
        : this.skills;
    },
  },
  created() {
    this.username = this.$store.getters.getUsername;
    this.isAdmin = this.$store.getters.getIsAdmin;
    axios({
      method: 'POST',
      url: 'http://localhost:8000/register',
      data: {
        action: 'email_needed',
        username: this.username,
      },
    }).then((response) => {
      this.email = response.data.email;
    });
    this.fillSkills();
  },
  watch: {
    username() {
      const inputbox = document.querySelector<HTMLElement>('.nusername');
      if (this.rusername === '') {
        inputbox.style.backgroundColor = 'white';
        inputbox.style.color = 'black';
      } else {
        axios({
          method: 'POST',
          url: 'http://localhost:8000/register',
          data: {
            username: this.username,
            action: 'username_check',
          },
        }).then((response) => {
          if (response.data) {
            if (response.data.message === 'username exists!'
            && this.username !== localStorage.getItem('username')) {
              inputbox.style.backgroundColor = 'red';
              inputbox.title = response.data.message;
              inputbox.style.color = 'white';
              this.$emit('emit-snackbar', response.data.message);
            } else {
              inputbox.style.backgroundColor = 'green';
              inputbox.style.color = 'white';
            }
          }
        });
      }
    },
  },
  methods: {
    fillSkills() {
      axios({
        method: 'POST',
        url: 'http://localhost:8000/register',
        data: {
          username: this.username,
          action: 'skills_check',
        },
      }).then((response) => {
        this.loading_added_skills = false;
        const skills = response.data.skills.replaceAll('[', '').replaceAll(']', '')
          .replaceAll("'", '').replaceAll(', ', ',');
        this.selected_skills = skills.split(',');
        axios({
          method: 'POST',
          url: 'http://localhost:8000/skills',
        }).then((selresponse) => {
          this.loading_all_skills = false;
          this.skills = selresponse.data.skills.filter(
            (skill) => !this.selected_skills.includes(skill),
          );
        });
      });
    },
    moveSelected(skill) {
      let toMove;
      if (skill.target) {
        toMove = skill.dataTransfer.getData('skill');
      } else {
        toMove = skill;
      }
      if (this.skills.indexOf(toMove) !== -1) {
        this.skills = this.skills.filter((arraySkill) => arraySkill !== toMove);
        this.selected_skills.push(toMove);
        setTimeout(() => {
          document.querySelector('#selected').scrollTop = document.querySelector('#selected').scrollHeight;
        }, 1);
      } else if (this.selected_skills.indexOf(toMove) !== -1) {
        this.selected_skills = this.selected_skills.filter((arraySkill) => arraySkill !== toMove);
        this.skills.push(toMove);
      }
    },
    updateUser() {
      const inputbox = document.querySelector<HTMLElement>('.nusername');
      if (!(inputbox.style.backgroundColor === 'red')
      && this.selected_skills.length > 0) {
        axios({
          method: 'POST',
          url: 'http://localhost:8000/register',
          data: {
            username: this.username,
            ousername: localStorage.getItem('username'),
            email: this.email,
            skills: this.selected_skills,
            action: 'update',
          },
        }).then((response) => {
          if (response.data.message === 'successfully updated!') {
            this.$store.dispatch('setUsername', response.data.user);
            localStorage.setItem('username', this.username);
            this.$emit('emit-snackbar', response.data.message);
            this.fillSkills();
          }
        });
      } else if (this.selected_skills.length === 0) {
        this.$emit('emit-snackbar', 'Please select at least one skill!');
      } else {
        this.$emit('emit-snackbar', 'Please correct the errors first!');
      }
    },
    changePassword() {
      axios({
        method: 'POST',
        url: 'http://localhost:8000/register',
        data: {
          username: this.username,
          password: this.password,
          new_password: this.new_password,
          action: 'change_password',
        },
      }).then((response) => {
        this.$emit('emit-snackbar', response.data.message);
        this.password = '';
        this.new_password = '';
      });
    },
  },
  data() {
    return {
      username: '',
      isAdmin: '',
      email: '',
      skills: [],
      search_term: '',
      selected_skills: [],
      password: '',
      new_password: '',
      loading_all_skills: true,
      loading_added_skills: true,
    };
  },
});
