
import { defineComponent } from 'vue';
import axios from 'axios';
import Loading from '../components/Loading.vue';

export default defineComponent({
  name: 'Log',
  computed: {
  },
  created() {
    this.isAdmin = this.$store.getters.getUsername;
    if (!this.isAdmin) this.$router.push('/recommend');
    this.requestLog();
  },
  components: {
    Loading,
  },
  watch: {
  },
  methods: {
    clearLog() {
      this.clear = true;
      this.requestLog();
    },
    clearAll() {
      this.clear = 'all';
      this.requestLog();
    },
    changeSource(e) {
      this.source = e.srcElement.value;
      this.requestLog();
    },
    requestLog() {
      this.loading = true;
      this.messages = [];
      axios({
        url: `http://localhost:8000/logs/${this.source}/`,
        method: 'POST',
        data: {
          clear: this.clear,
        },
      })
        .then((response) => {
          if (response.data) {
            const messages = response.data.contents.split('\n');
            const logContents = [];
            messages.forEach((message) => {
              let priority = 'info';
              if (message.includes('ERROR')) priority = 'error';
              logContents.push({
                message,
                priority,
                timestamp: '',
              });
            });
            this.messages = logContents;
          } else {
            this.messages = [];
          }
          this.loading = false;
          this.clear = false;
        });
      this.eventSource = new EventSource(`http://localhost:8000/events/${this.source}/`);
      this.eventSource.onmessage = (e) => {
        const data = JSON.parse(e.data);
        this.current_message = data.message;
        this.message_priority = data.type;
        const currentdate = new Date();
        const timestamp = `${currentdate.getFullYear()}-
          ${currentdate.getMonth() + 1}-
          ${currentdate.getDate()} @ 
          ${currentdate.getHours()}:
          ${currentdate.getMinutes()}:
          ${currentdate.getSeconds()}`;
        this.messages.unshift({
          message: this.current_message,
          priority: this.message_priority,
          timestamp,
        });
      };
    },
  },
  data() {
    return {
      messages: [],
      isAdmin: '',
      source: 'log_main',
      clear: false,
      eventSource: '',
      loading: true,
    };
  },
});
