
import { defineComponent } from 'vue';
import { showSnackbar } from './misc/functions';

export default defineComponent({
  name: 'Root',
  components: {
  },
  watch: {
  },
  methods: {
    emitSnackbar(msg) {
      const snackbar = document.getElementById('snackbar');
      this.message = msg;
      showSnackbar(snackbar);
      setTimeout(this.resetMessage, 3000);
    },
    resetMessage() {
      this.message = '';
    },
    fillJobs(jobArray) {
      this.recJobs = jobArray;
    },
  },
  data() {
    return {
      message: '',
      recJobs: [],
    };
  },
});
