
import { defineComponent } from 'vue';
import Register from '../components/Register.vue';
import Login from '../components/Login.vue';

export default defineComponent({
  name: 'RegisterLogin',
  emits: ['emit-snackbar'],
  props: ['message'],
  components: {
    Register,
    Login,
  },
  methods: {
    emitSnackbar(message) { this.$emit('emit-snackbar', message); },
  },
});
